import { BASE_API_URL } from '../environment/environment.js'

export default {
    name: 'Home',
    data() {
        return {  
            properties: null  
        }
    },
    fetch() {
        const url = `${BASE_API_URL}/properties`
        fetch(url)
            .then(res => res.json())
            .then(data => {
                this.properties = data
            })
    }
}
